import sizes from './sizes';
import chroma from 'chroma-js';

const styles = {
  DraggableColorBox: {
    backgroundColor: (props) => props.color,
    width: '20%',
    height: '25%',
    margin: '0 auto',
    display: 'inline-block',
    position: 'relative',
    cursor: 'pointer',
    marginBottom: '-3.5px',

    // Seem to need to do this, rather than relying on the deleteIcon class
    '& svg': {
      color: 'rgba(0, 0, 0, 0.5)',
      transition: 'all 0.3s ease-in-out',
    },

    '&:hover svg': {
      color: 'white',
      transform: 'scale(1.5)',
    },

    [sizes.down('lg')]: {
      width: '25%',
      height: '20%',
    },

    [sizes.down('md')]: {
      width: '50%',
      height: '10%',
    },

    [sizes.down('sm')]: {
      width: '100%',
      height: '5%',
    },
  },

  boxContent: {
    position: 'absolute',
    width: '100%',
    left: '0px',
    bottom: '0px',
    padding: '10px',
    color: (props) =>
      chroma(props.color).luminance() <= 0.08
        ? 'rgba(255, 255, 255, 0.8)'
        : 'rgba(0, 0, 0, 0.6)',
    letterSpacing: '1px',
    textTransform: 'uppercase',
    fontSize: '12px',

    // Unique to DraggableColorBox over ColorBox
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },

  // Applies properly to the DeleteForeverIcon, but doesn't allow our "&:hover svg" class to transition properly
  // deleteIcon: {
  // color: 'rgba(0, 0, 0, 0.5)',
  // transition: 'all 1s ease-in-out',
  // },
};

export default styles;
